<template>
  <b-form class="form-refund" @submit="onSubmit">
    <h3 class="form-refund__title">
      ,     
    </h3>
    <b-form-row>
      <b-col md="4">
        <b-form-group>
          <b-input placeholder=" "
                   v-model="formRefund.name"
          />
        </b-form-group>
      </b-col>
      <b-col md="4">
        <b-form-group>
          <b-input placeholder=" "
                   v-model="formRefund.lastName"
          />
        </b-form-group>
      </b-col>
      <b-col md="4">
        <b-form-group>
          <b-input placeholder=" e-mail"
                   v-model="formRefund.email"
          />
        </b-form-group>
      </b-col>
    </b-form-row>
    <b-form-row>
      <b-col md="4">
        <b-form-group>
          <b-input placeholder=""
                   v-model="formRefund.tel"
          />
        </b-form-group>
      </b-col>
      <b-col md="4">
        <b-form-group>
          <b-input placeholder=" "
                   v-model="formRefund.orderId"
          />
        </b-form-group>
      </b-col>
      <b-col md="4">
          <b-form-group>
            <b-form-datepicker
                locale="ru"
                placeholder=" "
                v-model="formRefund.date"
                :max="new Date()"
                date-format-options="{ 'day': 'numeric', 'month': 'numeric', 'year': 'numeric' }"
                hide-header="true"
            />
          </b-form-group>
      </b-col>
    </b-form-row>
    <b-form-row>
      <b-col md="6">
        <b-form-group>
          <b-input placeholder=" "
                   v-model="formRefund.productName"
          />
        </b-form-group>
      </b-col>
      <b-col md="6">
        <b-form-group>
          <b-input placeholder="/ "
                   v-model="formRefund.productId"
          />
        </b-form-group>
      </b-col>
    </b-form-row>
    <b-form-group>
      <b-textarea placeholder="  "
                  v-model="formRefund.comment"
      />
    </b-form-group>
    <b-btn type="submit" class="btn btn-blue mb-2">
       
    </b-btn>
    <p>
          ,    
    </p>
  </b-form>
</template>

<script>
export default {
  name: "FormRefund",
  data() {
    return {
      formRefund: {
        name: '',
        lastName: '',
        email: '',
        tel: '',
        orderId: '',
        date: null,
        productName: '',
        productId: '',
        comment: ''
      }
    }
  },
  methods: {
    onSubmit(event) {
      event.preventDefault();
      alert(JSON.stringify(this.formRefund));
      this.onReset();
    },
    onReset(event) {
      event.preventDefault();
      this.formRefund = {
        name: '',
        lastName: '',
        email: '',
        tel: '',
        orderId: '',
        date: null,
        productName: '',
        productId: '',
        comment: ''
      }
    }
  }
}
</script>

